/* General Container Styling */
.todo-container {
  background: linear-gradient(to right, #e1f5f2, #f3e5f5); /* Light teal and lavender gradient */
  padding: 20px;
  border-radius: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: 30px auto;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  width: 90%; /* Full width on small screens */
}

/* Header */
.todo-container h1 {
  color: #4a148c;
  font-size: 2rem; /* Adjust font-size for responsiveness */
  font-weight: bold;
  margin-bottom: 20px;
}

/* Input Container */
.input-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  flex-direction: column; /* Stack elements on small screens */
}

input[type="text"] {
  padding: 15px;
  width: 100%; /* Full width on smaller screens */
  max-width: 75%; /* Limit width on larger screens */
  margin-bottom: 10px;
  border: 2px solid #80deea;
  border-radius: 10px;
  font-size: 1rem;
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: border-color 0.3s ease;
}

input[type="text"]:focus {
  border-color: #4a148c;
}

.add-task-btn {
  background-color: #26c6da;
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 50px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease, transform 0.2s ease;
  width: 100%; /* Full width on smaller screens */
  max-width: 200px; /* Limit width on larger screens */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
}

.add-task-btn:hover {
  background-color: #0097a7;
  transform: scale(1.05);
}

/* Task List */
.task-list {
  list-style-type: none;
  padding: 0;
}

.task-item {
  background-color: #ffffff;
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
  cursor: pointer;
  flex-wrap: wrap; /* Wrap content if needed */
}

.task-item:hover {
  background-color: #f3e5f5;
}

.task-item.completed span {
  text-decoration: line-through;
  color: #90a4ae;
}

.task-item span {
  flex-grow: 1;
  text-align: left;
  font-size: 1rem;
  color: #212121;
  padding-right: 15px;
}

/* Checkbox */
.task-checkbox {
  margin-right: 15px;
  transform: scale(1.3);
  cursor: pointer;
}

/* Buttons (Edit/Delete/Save) */
.save-btn, .edit-btn, .delete-btn {
  padding: 8px 12px;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  display: flex;
  align-items: center;
  font-size: 1rem;
  transition: background-color 0.3s ease, transform 0.2s ease;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.save-btn {
  background-color: #43a047;
  color: white;
}

.save-btn:hover {
  background-color: #388e3c;
  transform: scale(1.05);
}

.edit-btn {
  background-color: #29b6f6;
  color: white;
}

.edit-btn:hover {
  background-color: #0288d1;
  transform: scale(1.05);
}

.delete-btn {
  background-color: #e53935;
  color: white;
}

.delete-btn:hover {
  background-color: #d32f2f;
  transform: scale(1.05);
}

/* Progress Bar */
.progress-bar {
  width: 100%;
  background-color: #eeeeee;
  border-radius: 15px;
  height: 20px;
  margin-bottom: 20px;
  box-shadow: inset 0 4px 10px rgba(0, 0, 0, 0.1);
}

.progress {
  height: 100%;
  background-color: #26a69a;
  border-radius: 15px;
  transition: width 0.4s ease;
}

/* Notification */
.notification {
  color: #d32f2f;
  font-size: 1rem;
  margin-bottom: 10px;
  font-weight: 500;
}

/* Responsive Design using Media Queries */
@media (max-width: 768px) {
  .task-item {
    flex-direction: column; /* Stack content on smaller screens */
    align-items: flex-start;
  }

  .task-item span {
    margin-bottom: 10px; /* Add spacing between elements */
  }

  .task-checkbox {
    margin-right: 0;
    margin-bottom: 10px;
  }

  .save-btn, .edit-btn, .delete-btn {
    padding: 8px;
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .todo-container h1 {
    font-size: 1.5rem; /* Smaller font size for mobile screens */
  }

  .add-task-btn {
    font-size: 14px;
  }

  .save-btn, .edit-btn, .delete-btn {
    font-size: 12px; /* Smaller buttons for mobile */
  }
}
